import {
	detail,collect,nocollect
} from '@/api/api.js'
import {
	access
} from '@/api/request.js'
import loading from '@/components/loading/loading.vue'

export default {
	components: {
		loading
	},
	/**
	 * 页面的初始数据
	 */
	data() {
		return {
			title: '',
			content: '',
			id: '',
			obj: {},
			isLoad: true,
			paddingTop: getApp().globalData.windowHeight / 2 - 100,
			isCollect: false,
			authors: '',
			typeName: '',
			//是否是iphoneX
			isIphoneX: getApp().globalData.model.indexOf('iPhone X') != -1,
		}
	},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		console.log(options)
		if (!options.id) {
			uni.showToast({
				title: 'param error',
				icon: 'none'
			})
			return
		} else {
			this.id = options.id
		}
		if (options.title) {
			this.title = options.title
		}
		this.load()
		// console.log('detail.....',katex)			
	},

	methods: {
		collect() {
			uni.showLoading({
				title: 'loading...'
			});
			var $this = this
			var apiObj = collect(this.id)
			access(apiObj, function(res) {
				console.log('collect', res)
				uni.hideLoading()
				$this.isCollect = true
			}, function() {
				uni.hideLoading()
			})				
		},
		nocollect() {
			uni.showLoading({
				title: 'loading...'
			});
			var $this = this
			var apiObj = nocollect(this.id)
			access(apiObj, function(res) {
				console.log('nocollect', res)
				uni.hideLoading()
				$this.isCollect = false
			}, function() {
				uni.hideLoading()
			})
		},
		download(){
			
		},
		load: function() {
			uni.showNavigationBarLoading()
			this.isLoad = true
			var $this = this
			var apiObj = detail(this.id)
			access(apiObj, function(res) {
				console.log('detail', res)
				$this.isLoad = false
				uni.hideNavigationBarLoading()
				$this.title = res.data.paper.title
				$this.content = res.data.paper.abs
				$this.isCollect = res.data.collect
				$this.typeName = res.data.paper.typeName
				$this.authors = res.data.paper.authors
				setTimeout(function(){
					console.log($this.isCollect)
				},1000)
			}, function() {
				$this.isLoad = false
				uni.hideNavigationBarLoading()
			})
		},
		goBack(){
			uni.navigateBack({
				delta:1
			})
		}
	},
}
